h1, p {
  margin: 0;
}

.hero-hidden {
  display: none;
}

.hero-section {
  min-width: 320px;
  min-height: 380px;
  background-image: linear-gradient(86.77deg, #111 30.38%, rgba(17, 17, 17, 0) 65.61%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.hero-section-random {
  position: relative;
}

.hero-container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.hero-img {
  width: 320px;
  min-height: 380px;
  object-fit: cover;
  z-index: -3;
  position: absolute;
  top: 0;
  left: 0;
}

.hero-img-random {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -3;
  position: absolute;
  top: 0;
  left: 0;
}

.hero-title {
  max-width: 199px;
  align-items: center;
  margin-bottom: 8px;
  padding-top: 40px;
  font-family: Roboto-700, sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
  display: flex;
}

.hero-title-lib {
  max-width: 235px;
}

.hero-title-random {
  margin-bottom: 12px;
}

.hero-text {
  max-width: 177px;
