}

.hero-text-lib {
  max-width: 249px;
}

.hero-text-random {
  margin-top: 16px;
}

.hero-btn {
  min-width: 110px;
  height: 40px;
  background: var(--gradient-1);
  text-align: center;
  color: var(--black);
  border: 0;
  border-radius: 74px;
  align-items: center;
  padding: 12px 24px;
  font-family: Roboto-500, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.17;
  display: flex;
}

.hero-btn:hover {
  border-image-source: var(--gradient-1);
  color: var(--orange);
  background: none;
  border: 1px solid;
  outline: none !important;
}

.hero-random {
  margin-bottom: 12px;
}

.hero-random-btn {
  gap: 8px;
  display: flex;
}

.popup {
  width: 100%;
  height: 100%;
  z-index: 9;
  background-color: rgba(0, 0, 0, .8);
  position: fixed;
  top: 0;
  left: 0;
}

.popup-hidden {
  opacity: 0;
  visibility: hidden;
}

.popup-body {
  min-height: 100%;
  justify-content: center;
  align-items: center;
  padding: 30px 10px;
  display: flex;
}

.popup-content {
  background-color: var(--black);
  color: var(--white);
  width: 280px;
  height: 280px;
  border-radius: 16px;
  position: relative;
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.popup-content-default {
  padding: 39px 33px 0;
}

.popup-content-render {
  position: relative;
}

.popup-video {
  width: 260px;
  position: absolute;
  top: 56px;
  left: 10px;
}

.popup-icon-out {
  fill: var(--black);
