}

.container-library {
  width: 100%;
  margin: auto;
  padding: 0 20px;
}

.dropdown-library {
  margin: 0 0 16px;
  display: inline-block;
  position: relative;
}

.dropdown-toggle-library {
  width: 173px;
  cursor: pointer;
  border: 1px solid var(--white);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  padding: 10px 56px 10px 16px;
  font-size: 14px;
  line-height: 16px;
}

.dropdown-menu-library {
  width: 172px;
  background-color: var(--dropdown);
  border: 1px solid var(--white);
  z-index: 5000;
  border-radius: 8px;
  display: none;
  position: absolute;
}

.dropdown-item-library {
  cursor: pointer;
  border-bottom: 1px solid var(--white);
  width: 100%;
  padding: 14px 66px 10px 20px;
  font-size: 14px;
  line-height: 20px;
}

.dropdown-item-library:hover, .dropdown-item-library:active {
  color: var(--orange);
}

.dropdown-item-library:last-child {
  border: none;
}

.dropdown-toggle-library:before {
  content: "";
  width: 16px;
  height: 16px;
  opacity: 10;
  fill: var(--white);
  background-image: url("chevron-down.168da858.svg");
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: 11px;
  left: 142px;
}

.dropdown-toggle-library.rotated:before {
  transform: rotate(180deg);
}

.dropdown-item-library.\.dropdown-item-library-color {
  color: var(--orange);
}

.form_item_library {
  margin-right: 10px;
  padding-bottom: 10px;
  position: absolute;
}

.movies-gallery {
  color: #fff;
  width: 280px;
