ul {
  margin: 0;
}

.upcoming .container {
  padding-left: 20px;
  padding-right: 20px;

}

.upcoming_title {
  margin-bottom: 24px;
  margin-top: 32px;
  font-family: 'Roboto-500', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.2;
}


.upcoming-error-container {
  padding-top: 80px;
  margin-left: auto;
  margin-right: auto;
}


.upcoming_error_container_text {
  margin-bottom: 24px;
  font-family: 'Roboto-400', sans-serif;

  font-size: 20px;
  line-height: 1.2;

}


.upcoming-img {
  display: block;
  width: 100%;
  border-radius: 5px;
}

.upcoming_name {
  margin-top: 24px;
  margin-bottom: 20px;
  font-family: 'Roboto-500', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.15;
  text-transform: uppercase;
}


.upcoming_list_item {
  display: flex;
  padding-bottom: 12px;

font-family: 'Roboto-500', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.2;
}

.upcoming_list_text {
  display: flex;

}

.upcoming_list_date {
  /* color: var(--orange); */
  margin-left: 58px;
}

.upcoming_list_vote {
  margin-left: 62px;
    /* color: var(--white); */
}

.vote {
  font-size: 14px;
  line-height: 1.14;
  padding: 1px 8px;
  background-color: var(--white);
  color: var(--black);
  border-radius: 5px;
}

.upcoming_list_pop {
    
  /* color: var(--white); */
  margin-left: 78px;
}

.upcoming_list_genre {
  color: var(--white);

  margin-left: 108px;

}

.upcoming_content_title {
  margin-top: 20px;
  font-family: 'Roboto-500', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.2;
  align-items: center;
  text-transform: uppercase;
}

.upcoming_content_text {
  margin-top: 16px;
  font-family: 'Roboto-400', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.43;
  color: var(--silver);
}


.upcoming_content_btn {
  display: block;
  margin-top: 20px;
  padding: 12px 24px;
 

  font-family: 'Roboto-500', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16;
  border: 1px solid var(--brend-color);
  background: var(--gradient-1);
  border-radius: 74px;
  color: var(--black);
  cursor: pointer;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
  background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

}

.upcoming_content_btn:hover,
.upcoming_content_btn:focus {
  color: var(--orange);
  background: #fff;
  border: 1px solid var(--gradient-1);
}


@media screen and (min-width: 768px) {

  .upcoming .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .upcoming_title {
    margin-top: 44px;
    font-size: 24px;
    line-height: 1.16;
  }


  .upcoming_content {
    width: 704px;
  }

  .upcoming_error_container_text {

    margin-left: auto;
    margin-right: auto;
    font-size: 24px;
    text-align: center;
  }


  .upcoming_img {
    width: 704px;
    height: auto;
  }


  .upcoming_name {
    font-size: 28px;
    line-height: 1.18;
  }


  .upcoming_thumb {
    display: flex;
    /* justify-content: space-between; */
    margin-left: 4px;
 
  }


  .left {

    /* margin-right: 118px; */
    width: 350px;
  }

  .upcoming_content_title,
  .upcoming_content_text {
    margin-left: 4px;
  }


}

@media screen and (min-width: 1280px) {

  .upcoming .container {
    width: 1280px;
  }

  .upcoming_title {
    margin-top: 60px;
    margin-bottom: 28px;
  }


  .upcoming_error_container_text {
    margin-top: 0;
    margin-bottom: 20px;
  }

  .upcoming-film {
    display: flex;
    gap: 16px;
  }

  .upcoming_name {
    margin-top: 0;
    font-size: 32px;
    line-height: 1.19;
  }

  .upcoming_thumb {
    display: block;
  }


}